html {
  scroll-behavior: smooth;
}

body {
  color: var(--content-black-color);
  background-image: linear-gradient(180.11deg, var(--bg-white-color1) 10.73%, var(--bg-white-color2) 38.02%, var(--bg-white-color1) 71.11%, var(--bg-white-color2) 99.91%);
